body {
   margin: 0;
   font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
   -webkit-font-smoothing: antialiased;
   -moz-osx-font-smoothing: grayscale;
   overflow: hidden;
}

code {
   font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
}

.App {
   text-align: center;
   overflow: hidden;
   outline: none;
}

.home {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
  background-color: #c5d86d;
  flex-direction: column;
}

.heart-icon {
  width: 5vw;
  height: auto;
  transition: 0.3s ease-in-out;
  z-index: 2;
}

.heart {
  z-index: 2;
}

.overlay-left {
  background-color: #49516f;
  width: 50vw;
  height: 100vh;
  position: absolute;
  top: 0px;
  left: 0px;
  transition: 0.3s ease-in-out forwards;
  z-index: 2;
}

.overlay-right {
  background-color: #49516f;
  width: 50vw;
  height: 100vh;
  position: absolute;
  top: 0px;
  left: 50vw;
  z-index: 2;
}

.heart :hover {
  transform: scale(1.2);
  cursor: pointer;
}

.overlay-hovered.overlay-left {
  animation: go-left 0.3s ease-in-out forwards;
}

.overlay-hovered.overlay-right {
  animation: go-right 0.3s ease-in-out forwards;
}

.overlay-not-hovered.overlay-left {
  transform: translateX(-50vw);
  animation: go-left-back 0.3s ease-in-out forwards;
  animation-delay: 0.3s;
}

.overlay-not-hovered.overlay-right {
  transform: translateX(50vw);
  animation: go-right-back 0.3s ease-in-out forwards;
  animation-delay: 0.3s;
}

.hide {
  opacity: 1;
  animation: text-hide 0.3s ease-in-out forwards;
}

.reveal {
  opacity: 0;
  animation: text-reveal 0.3s ease-in-out forwards;
}

.hidden {
  display: none;
}

.hide-delay {
  animation: text-hide 0.3s ease-in-out forwards;
  animation-delay: 0.3s;
}

.reveal-delay {
  opacity: 0;
  animation: text-reveal 0.3s ease-in-out forwards;
  animation-delay: 0.3s;
}

@keyframes go-left {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(-50vw);
  }
}

@keyframes go-left-back {
  0% {
    transform: translateX(-50vw);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes go-right {
  0% {
    transform: translateX(0);
  }
  100% {
    transform: translateX(50vw);
  }
}

@keyframes go-right-back {
  0% {
    transform: translateX(50vw);
  }
  100% {
    transform: translateX(0);
  }
}

@keyframes text-hide {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes text-reveal {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.text {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  z-index: 2;
}

.names {
  opacity: 0;
}

.iulia {
  position: absolute;
  top: 0;
  left: 0;
  width: 50vw;
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

.vlad {
  position: absolute;
  top: 0;
  left: 50vw;
  width: 50vw;
  height: 100vh;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.iulia p,
.vlad p {
  font-size: 8vw;
  letter-spacing: 5px;
  font-weight: normal;
  z-index: 1;
}

p {
  color: #04080f;
}
