.img-gallery {
  width: 100vw;
  height: 100vh;
  display: -ms-grid;
  display: grid;
      grid-template-areas: ". big big big big big ." ". big big big big big ." ". big big big big big ." ". big big big big big ." ". small small small small small .";
  -ms-grid-rows: 1fr 1fr 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  -ms-grid-columns: 5vw 1fr 1fr 1fr 1fr 1fr 5vw;
      grid-template-columns: 5vw 1fr 1fr 1fr 1fr 1fr 5vw;
  background-color: #fff8f0;
}

.img-gallery .small-imgs {
  -ms-grid-row: 5;
  -ms-grid-column: 2;
  -ms-grid-column-span: 5;
  grid-area: small;
  display: flex;
}

.img-gallery .small-imgs img {
  border-radius: 5%;
  margin: auto;
  width: 10vw;
  height: auto;
  transition: 0.1s ease-in-out;
}

.img-gallery .big-img {
  display: flex;
  cursor: pointer;
  -ms-grid-row: 1;
  -ms-grid-row-span: 4;
  -ms-grid-column: 2;
  -ms-grid-column-span: 5;
  grid-area: big;
}

.img-gallery .big-img img {
  border-radius: 1%;
  margin: auto;
  width: 50vw;
  height: auto;
}

.hidden {
  display: none;
}

.img-text {
  background: none;
  position: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90vw;
  height: 80vh;
}

.img-text p {
  text-align: center;
  outline: none;
}

.show {
  transform: scaleX(1);
}

.not-show {
  transform: scaleX(0);
}

.noSelect {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

@keyframes disappear {
  0% {
    transform: scaleX(1);
  }
  100% {
    transform: scaleX(0);
  }
}

@keyframes appear {
  0% {
    transform: scaleX(0) scaleY(1);
  }
  75% {
    transform: scaleX(1.05) scaleY(1.05);
  }
  100% {
    transform: scaleX(1) scaleY(1);
  }
}

.appear {
  animation: appear 0.3s ease-out forwards 0.2s;
  transform: scaleX(0);
}

.disappear {
  animation: disappear 0.2s ease-in-out forwards;
}

.hovered {
  opacity: 0.75;
  transform: scale(1.05);
  cursor: pointer;
}

.gallery-text-wrapper {
  display: flex;
  flex-direction: column;
  align-items: space-between;
  justify-content: space-between;
}

.gallery-text {
  font-size: 20px;
  line-height: 50px;
  margin-left: 100px;
  margin-right: 100px;
}

@media only screen and (max-width: 850px) {
  .gallery-text {
    font-size: 12px;
    line-height: 25px;
    margin-left: 30px;
    margin-right: 30px;
  }
}
